@import 'src/styles/helpers.scss';

.awards {
  position: relative;
  z-index: 0;
  padding: rem(62px) 0 rem(100px);
  @media #{media("<")} {
    padding: rem(20px) 0 rem(50px);
  }

  background-color: #101010;
  overflow: hidden;
}

.wrapper {
  margin: 0 auto;
  max-width: rem(500px);
  @media #{media("<", m)} {
    max-width: rem(309px);
  }
}

.count {
  text-align: center;
  color: color(white);
  letter-spacing: -0.06em;
  @include fsl(300px, 90%);
  @media #{media("<")} {
    
    @include fsl(130px);
  }
}

.caption {
  text-align: center;
  letter-spacing: -0.04em;
  color: color(gray);
  @include fsl(40px, 90%);
  @media #{media("<")} {
    @include fsl(24px);
    letter-spacing: -0.02em;
  }
}

.bg {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: -1;

  display: flex;
  align-items: center;
}

.grid {
  margin: rem(60px) auto 0;
  @media #{media("<")} {
    margin: rem(32px) 0 0;
    width: auto;
    grid-template-columns: repeat(2, 1fr);
    column-gap: rem(11px);
    row-gap: 0;
  }
  width: rem(1025px);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: rem(22px);
}

.item {
  position: relative;
  z-index: 0;
  @media #{media("<")} {
    margin-top: -1px;
    height: rem(209px);
  }
  height: rem(224px);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.icon {
  
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(120px)};

    width: var(--size);
    height: var(--size);
  }
}

.name {
  margin-bottom: rem(12px);
  @media #{media("<")} {
    margin-bottom: rem(8px);
    letter-spacing: -0.01em;
    @include fsl(14px);
  }
  letter-spacing: -0.02em;
  color: color(gray);
  @include fsl(18px, 120%);
}

.num {
  color: color(white);
  letter-spacing: -0.05em;
  @include fsl(48px, 90%);
  @media #{media("<")} {
    @include fsl(40px);
  }
  span {
    margin-right: rem(5px);
    display: inline-block;
    transform: translateY(rem(-4px));
  }
}

.img {
  position: absolute;
  z-index: -1;

  display: flex;
  align-items: center;

  &.left {
    left: rem(-251px);
    top: rem(-704px);
    @media #{media("<")} {
      left: rem(-87px);
      top: rem(-150px);
      width: rem(374px);
      height: rem(374px);
    }
    width: rem(1346px);
    height: rem(1346px);
  }

  &.right {
    right: rem(-305px);
    bottom: rem(-547px);
    @media #{media("<")} {
      right: -1px;
      bottom: rem(-96px);
      width: rem(376px);
      height: rem(376px);
    }
    width: rem(1352px);
    height: rem(1352px);
  }
}

.pic {
  transition: $transition;
  overflow: visible !important;


  & > div {
    transition: $transition;
  }

  &.left {
    width: rem(1346px);
    height: rem(1346px);
    @media #{media("<")} {
      width: rem(374px);
      height: rem(374px);
    }
  }
  &.right {
    width: rem(1352px);
    height: rem(1352px);
    @media #{media("<")} {
      width: rem(376px);
      height: rem(376px);
    }

    img {
      height: 99%;
    }
  }
}